<template>
  <div class="d-flex flex-column justify-space-between" style="height: 100%">
    <div>
      <h2 class="grey--text">Choose the right options for your business</h2>

      <v-card class="grey lighten-3 pa-3 rounded-0 mt-10">
        <div class="d-flex justify-space-between">
          <v-icon x-large color="black">
            mdi-check
          </v-icon>
          <div>
            <p class="subtitle-2">
              Shin e Go Pay Monthly
              <v-icon color="black" small>
                mdi-help-circle-outline
              </v-icon>
            </p>
            <div class="subtitle-2 d-flex align-center">
              <label for="qty" class="mr-4 font-weight-bold">Enter the number of users</label>
              <input
                  id="qty"
                  type="number"
                  class="qty-input text-center"
                  min="1"
                  @change="handleChange"
                  v-model="qty"
              >
            </div>
          </div>
          <div class="align-self-end font-weight-bold">
            £{{ subtotal }} pcm
          </div>
        </div>
      </v-card>
    </div>

    <div>
      <div class="mb-5">
        <v-divider></v-divider>
        <table class="ml-auto mt-4">
          <tr class="font-italic">
            <td>Vat (@{{ vat }}%)</td>
            <td class="text-right pl-10">£{{ tax }}</td>
          </tr>
          <tr class="font-weight-bold">
            <td>Monthly Amount</td>
            <td class="text-right pl-10">£{{ total }}</td>
          </tr>
        </table>
      </div>

      <v-btn @click="next" class="ml-auto d-block white--text" color="orange">
        NEXT
      </v-btn>
    </div>
  </div>
</template>

<script>
const PRICE = 150;
const VAT = 20;

export default {
  name: "OptionSelect",
  data () {
    return {
      qty: 1,

      price: PRICE,
      vat: VAT
    };
  },
  methods: {
    next () {
      this.$emit('next', { qty: this.qty });
    },
    handleChange () {
      if (isNaN(parseInt(this.qty)) || this.qty < 1) {
        this.qty = 1;
      }
    }
  },
  computed: {
    subtotal () {
      return this.qty * this.price;
    },
    tax () {
      return this.subtotal * this.vat / 100;
    },
    total () {
      return this.subtotal + this.tax;
    }
  }
};
</script>

<style scoped lang="scss">
.qty-input {
  background-color: #fff;
  padding: 3px 8px;
  max-width: 40px;
  border-radius: 5px;
  border: 1px solid #ccc;
  /* Chrome, Safari, Edge, Opera */
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  /* Firefox */
  &[type=number] {
    -moz-appearance: textfield;
  }
}
</style>
