<template>
  <v-container fluid class="signup d-flex justify-space-between align-center flex-column">
    <Header/>
    <div>
      <h2 class="white--text text-center mb-4 text-h6 text-sm-4">Sign up to Shine Go today - enterprise grade, fully featured, Video Recruitment software with affordable, flexible monthly payment plans</h2>
      <div class="d-flex justify-center">

        <v-col md="10" cols="12">
          <v-card outlined tile class="pa-md-5 pa-0 d-flex flex-md-row flex-sm-column flex-column">
            <v-col md="6" sm="12">
              <img width="100%" src="../assets/images/did_you_know_sample.png" alt="">
            </v-col>
            <v-col md="6" sm="12">
              <SignUpForm @next="e => handleNext(2, e)" v-if="step === 1"/>
              <OptionSelect @next="e => handleNext(3, e)" v-if="step === 2"/>
              <PaymentDetails @next="e => handleNext(4, e)" v-if="step === 3"/>
              <WelcomeMessage @complete="handleComplete" v-if="step === 4"/>
            </v-col>
          </v-card>
        </v-col>
      </div>
    </div>
  </v-container>
</template>

<script>
import SignUpForm from '@/components/SignUpForm';
import OptionSelect from "@/components/OptionSelect";
import Header from "@/components/Header";
import PaymentDetails from "@/components/PaymentDetails";
import WelcomeMessage from "@/components/WelcomeMessage";

export default {
  name: 'SignUp',
  components: { WelcomeMessage, PaymentDetails, Header, OptionSelect, SignUpForm },
  data () {
    return {
      step: 1,
      data: {}
    };
  },
  methods: {
    handleNext (step, data) {
      this.step = step;
      this.data = { ...this.data, ...data };
    },
    handleComplete () {
      console.log(this.data);
    }
  }
};
</script>

<style lang="scss" scoped>
.signup {
  height: 100%;
  position: relative;
  background-image: url("../assets/images/SG Background.png");
  //background-image: linear-gradient(to bottom right, #f6a201, #e84b1c);
}
</style>
