<template>
  <div class="d-flex flex-column justify-space-between" style="height: 100%">
    <h2 class="grey--text">Create an Account</h2>
    <validation-observer
        ref="observer"
        v-slot="data"
    >
      <form @submit.prevent="submit">
        <validation-provider v-slot="{ errors }" name="Company Name" rules="required">
          <v-text-field v-model="company_name" :error-messages="errors" label="Company Name" required>
            <v-icon slot="prepend">mdi-account</v-icon>
          </v-text-field>
        </validation-provider>

        <validation-provider v-slot="{ errors }" name="Company email Address" rules="required|email">
          <v-text-field autocomplete="off"  v-model="company_email" :error-messages="errors" label="Company email Address" required>
            <v-icon slot="prepend">mdi-account</v-icon>
          </v-text-field>
        </validation-provider>

        <validation-provider v-slot="{ errors }" name="Your First Name" rules="required">
          <v-text-field v-model="first_name" :error-messages="errors" label="Your First Name" required>
            <v-icon slot="prepend">mdi-account</v-icon>
          </v-text-field>
        </validation-provider>

        <validation-provider v-slot="{ errors }" name="Your Last Name" rules="required">
          <v-text-field v-model="last_name" :error-messages="errors" label="Your Last Name" required>
            <v-icon slot="prepend">mdi-account</v-icon>
          </v-text-field>
        </validation-provider>

        <validation-provider v-slot="{ errors }" name="Country" rules="required">
          <v-select v-model="country" :items="countries" :error-messages="errors" label="Country of residence" data-vv-name="Country of residence" required>
            <v-icon slot="prepend">mdi-account</v-icon>
          </v-select>
        </validation-provider>
        <validation-provider v-slot="{ errors }" name="Password" :rules="{
              required: true,
              min: 8,
              regex: new RegExp('^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})')
            }">
          <v-text-field
              autocomplete="off"
              v-model="password"
              :append-icon="showPass ? 'mdi-eye' : 'mdi-eye-off'"
              :type="showPass ? 'text' : 'password'"
              :error-messages="errors"
              name="password"
              label="Password"
              hint="Your password must be at least 8 Characters and contain at least 1 upper case letter, 1 lower case letter, 1 number & a special character (!@#$%^&*)"
              @click:append="showPass = !showPass"
          >
            <v-icon slot="prepend">mdi-account</v-icon>
          </v-text-field>
        </validation-provider>

        <v-btn class="ml-auto d-block white--text" type="submit" :disabled="data.invalid" color="orange">
          NEXT
        </v-btn>
      </form>
    </validation-observer>
  </div>
</template>

<script>
import { required, email, min, regex } from 'vee-validate/dist/rules';
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate';

setInteractionMode('eager');

extend('required', {
  ...required,
  message: '{_field_} can not be empty'
});

extend('email', {
  ...email,
  message: 'Email must be valid'
});

extend('min', {
  ...min,
  message: '{_field_} should be at least 8 characters'
});

extend('regex', {
  ...regex,
  message: '{_field_} is not valid'
});

export default {
  name: 'SignUpForm',
  components: {
    ValidationProvider,
    ValidationObserver
  },
  data: () => ({
    company_name: '',
    company_email: '',
    first_name: '',
    last_name: '',
    country: '',
    password: '',

    countries: [
      'United Kingdom'
    ],

    showPass: false
  }),

  methods: {
    async submit () {
      await this.$refs.observer.validate();
      this.$emit('next', {
        company_name: this.company_name,
        company_email: this.company_email,
        first_name: this.first_name,
        last_name: this.last_name,
        country: this.country,
        password: this.password
      });
    }
  }
};
</script>

